// container cart
$main-color: #6394f8;
$light-text: #abb0be;
.container {
  margin: auto;
  width: 80%;
}

.badge {
  background-color: #6394f8;
  border-radius: 10px;
  color: white;
  display: inline-block;
  font-size: 12px;
  line-height: 1;
  padding: 3px 7px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}

.shopping-cart {
  width: 400px;
  position: fixed;
  border-radius: 9px;
  padding: 20px;
  background: #282525;
  overflow: scroll;
  overflow-x: scroll;
  z-index: 999999999;
  right: 30px;
  bottom: 0;
  height: 400px !important;
  scrollbar-color: #fff #000;
  scrollbar-width: thin;
  overflow-x: hidden;
  box-shadow: 2px 1px 10px 4px #e1dfdf;
}

.shopping-cart-header {
  border-bottom: none !important;
  padding-bottom: 15px;
  .shopping-cart-total {
    float: right;
  }
}
.shopping-cart-items {
  padding-top: 20px;
  margin: 0 auto;
  padding: 0;
  li {
    margin-bottom: 18px;
  }
  img {
    float: left;
    margin-right: 12px;
    width: 70px;
    height: 70px;
    object-fit: contain;
  }
  .item-name {
    display: block;
    padding-top: 10px;
    font-size: 16px;
  }
  .item-price {
    color: $main-color;
    margin-right: 8px;
  }
  .item-quantity {
    color: $light-text;
  }
}
.shopping-cart-header {
  // border-bottom: none !important;
  padding-bottom: none !important;
}

.shopping-cart:after {
  bottom: 100%;
  left: 89%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: rgb(194, 31, 31);
  border-width: 8px;
  margin-left: -8px;
}

.cart-icon {
  color: #515783;
  font-size: 24px;
  margin-right: 7px;
  float: left;
}

.button {
  background: $main-color;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  display: block;
  border-radius: 3px;
  font-size: 16px;
  margin: 25px 0 15px 0;
  &:hover {
    background: lighten($main-color, 3%);
  }
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.tab-pane {
  background: #354252 !important;
  border-radius: 5px;
}

